"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Switch } from "@/components/ui/switch";

export default function PrivacySecurityPage() {
  const [show2FA, setShow2FA] = useState(false);

  return (
    <div className="flex min-h-screen items-center justify-center bg-[#DCE9FB] p-4">
      <div className="w-full sm:max-w-[600px] md:max-w-[700px] lg:max-w-[750px] rounded-[40px] bg-white flex flex-col items-center shadow-2xl overflow-hidden transition-all duration-300 p-6">
        
        {/* Titlu */}
        <div className="w-full text-center mb-6">
          <h1 className="font-poppins font-bold text-[2rem] leading-snug bg-gradient-to-b from-[#0C3057] to-[#31649A] bg-clip-text text-transparent">
            Privacy and Security
          </h1>
          <p className="text-[1rem] text-black/70 mt-2">
            Manage your account security and privacy settings
          </p>
        </div>

        {/* Email Section */}
        <div className="w-full mb-6">
          <h2 className="font-semibold text-lg mb-2">Sign-in Email</h2>
          <Input type="email" placeholder="Email Address" className="w-full h-[50px] rounded-lg border border-gray-400 px-4" />
        </div>

        {/* Password Section */}
        <div className="w-full mb-6">
          <h2 className="font-semibold text-lg mb-2">Password</h2>
          <Input type="password" placeholder="********" className="w-full h-[50px] rounded-lg border border-gray-400 px-4 mb-2" />
          <Button className="w-full sm:w-[200px] bg-secondary text-white font-bold rounded-lg shadow-md">
            Change Password
          </Button>
        </div>

        {/* Phone Number / 2FA */}
        <div className="w-full mb-6 flex flex-col">
          <div className="flex items-center justify-between mb-2">
            <h2 className="font-semibold text-lg">2FA Authentication</h2>
            <Switch checked={show2FA} onCheckedChange={setShow2FA} />
          </div>
          <p className="text-sm text-gray-600">
            Enable 2FA to add an extra layer of protection. You’ll confirm your login with a code sent to your phone or email.
          </p>
        </div>

        {/* Control Your Privacy */}
        <div className="w-full mb-6">
          <h2 className="font-semibold text-lg mb-2">Control Your Privacy</h2>
          <div className="text-sm text-gray-700 space-y-2">
            <p>Last sign-in today at 19:45, Safari 192.168.1.101, iPhone 13 Pro</p>
            <p>Manage what others can see from your profile and review active sessions anytime.</p>
            <p>Total active sessions (3):</p>
            <ul className="list-disc ml-6">
              <li>ALL DESKTOP-6TIG6EC • Iasi, Romania</li>
              <li>iPhone 13 Pro • Iasi, Romania</li>
            </ul>
            <p>We keep your data private and never share it with third-parties.</p>
          </div>
        </div>

      </div>
    </div>
  );
}
